import fetch from './fetch'
import { Message } from 'element-ui'

//==========  hostList ==========
// 
export const getHostList = ()=> { 
    return fetch({
        url: '/_house_api/getHostList',
        method: 'post'
    })
}
// 
export const getCurrentHost = ()=> {
    return fetch({
        url: '/_house_api/getCurrentHost',
        method: 'post'
    })
}
// 
export const setCurrentHost = (host) => {
    return fetch({
        url: '/_house_api/set_currentHost',
        method: 'post',
        data: {
            host
        }
    })
}
// 
export const sendAddHost = (host) => {
    return fetch({
        url: '/_house_api/addHost',
        method: 'post',
        data: {
            host
        }
    })
}
// 
export const sendRemoveHost = (host) => {
    return fetch({
        url: '/_house_api/removeHost',
        method: 'post',
        data: {
            host
        }
    })
}

//========== apiData site ==========
// api
export const getApiData = (formData) => {
    return fetch({
        url: '/_house_api/getApiData',
        method: 'post',
        data: formData
    })
}
// urlurl  url
export const getApiUrlList = path => {
    return fetch({
        url: '/_house_api/getApiDataByPath',
        method: 'post',
        data: {
            path,
            filterKeys:['path']//url
        }
    })
}
// url 
export const getApiInfoByPath = (path) => {
    return fetch({
        url: '/_house_api/getApiDataByPath',
        method: 'post',
        data: {
            path
        }
    }).then(resList=>{
        if (!resList.length) {
            Message({
                message: ``,
                type: 'warning'
            })
        }
        return resList[0]||{}
    })
}
// api
export const updateOne_apiData = (url, setData) => {
    return fetch({
        url: '/_house_api/updateOne_apiData',
        method: 'post',
        data: {
            url,
            ...setData
        }
    })
}
// api
