<template>
    <div class="markup-content" v-cloak v-loading="loading">
      <el-row :gutter="20">
        <el-col :span='10'>
          <div style="font-size:14px">
            <div></div>
            <div>{{userInfo.currentHost}}</div>
          </div>
          <div style="margin-top:30px">
            <p> <br>
              <el-button size="mini" @click="clickUpdateApiList"></el-button>
              <el-button size="mini" @click="clickAddApi"></el-button>
              <el-button size="mini" @click="clickAllApiSetOrigin(true)"></el-button>
              <el-button size="mini" @click="clickAllApiSetOrigin(false)"></el-button>
              <el-button size="mini" @click="clickAllSetBackups(false)"></el-button>
            </p>
            <el-collapse v-model="currentApiUrl" @change="onChangeApi" accordion>
              <el-collapse-item 
                v-for="item in apiList" 
                :key="item._id" 
                :title="item.path" 
                :name="item.url"
              >
                <p style="display:flex">
                  <span style="color:#00BF6C;with:82px;"></span>
                  <span style="flex:1">
                    <span v-if="!isEditingDesc && item.desc" @click="isEditingDesc = true">{{item.desc}}</span>
                    <el-input v-else v-model="item.desc" placeholder="" @blur="onBlurInputApiDesc(item)" size="mini"></el-input>
                  </span>
                  <!-- <el-button size="mini" ></el-button> -->
                </p>
                <!-- collapse-item conent beigin -->
                <span style="color:#00BF6C;"></span>
                <el-radio-group v-model="item.isUseLocalData" @change="onChange_isUseLocalData(item)">
                  <el-radio :label="true"></el-radio>
                  <el-radio :label="false"></el-radio>
                </el-radio-group>
                <p>
                  <el-button @click="clickEditApi(item)" type="warning" size="mini" ></el-button>
                  <el-button @click="clickChangeApi(item)" size="mini" ></el-button>
                  <el-button @click="updateApi(item)" size="mini" ></el-button>
                  <el-button @click="clickRemoveApi(item)" type="danger" size="mini" style="float:right;margin-right:10px"></el-button>
                </p>
                <p style="color:#00BF6C">{{item.time}}</p>
                <!-- collapse-item conent end -->
              </el-collapse-item>
            </el-collapse>
          </div>    
        </el-col>
        <el-col :span='14' style="border:1px solid #eee;height:100%;background:#eee;border-radius:10px">
          <p>
            <el-button v-if="isUseLocalData && isChangeApiCont" @click="saveEdit" type="success" size="mini" ></el-button>
            <span v-if="isUseLocalData===false" style="color:#888;font-size:14px;"></span>
          </p>
          <el-input  
            v-model="apiContStr" 
            @click="clickApiCont"
            type="textarea" 
            :rows="27" 
            placeholder="">
          </el-input>
        </el-col>
      </el-row>
      <!-- dialog -->
      <el-dialog title="" :visible.sync="dialog.visible" width="80%">
         <el-form ref="dialogForm" :model="dialog.form" status-icon label-width="90px">
          <el-form-item label="path :" prop="path" :rules="{required:true,message:''}">
            <el-input 
              v-model="dialog.form.path" 
              placeholder="/publick/userInfo"
            ></el-input>
          </el-form-item>
          <el-form-item label=" :">
            <el-input 
              v-model="dialog.form.desc" 
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label=" :" prop="apiCont" :rules="{required:true,message:''}">
            <el-input 
              v-model="dialog.form.apiCont" 
              type="textarea" 
              rows="10" 
              placeholder="{ret:1,data:[]}"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialog.visible = false"> </el-button>
          <el-button type="primary" @click="clickSubmitAddApi"> </el-button>
        </div>
      </el-dialog>
      <!-- dialog api path  url -->
      <el-dialog title="" :visible.sync="dialogApi.visible" width="70%">
         <el-form ref="dialogApiForm" :model="dialogApi.form" status-icon label-width="90px">
          <el-form-item label="path :" prop="newPath" :rules="{required:true,message:''}">
            <el-input 
              v-model="dialogApi.form.newPath" 
              placeholder="/publick/userInfo"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogApi.visible = false"> </el-button>
          <el-button type="primary" @click="submitApiChangeRes"> </el-button>
        </div>
      </el-dialog>
    </div>
</template>
<script>
  import {getApiData,getCurrentHost,updateMany_apiDataByHost,change_isUseLocalData,updateOne_apiData_byId,removeOne_api,addOne_api,setUseAllBackups,sendAddAllNewBackups,getApiInfoByPath} from '../../api/control'
  import { format } from "../../utils";
  import { mapState, mapActions } from "vuex";
  export default {
    data() {
      return {
        isEditingDesc: false,
        dialog:{
          visible:false,
          form:{
            path:'',
            desc:'',
            apiCont:''
          }
        },
        dialogApi:{
          visible:false,
          form:{}
        },
        apiInfo:{},//api
        apiList:[],//
        apiContStr:'',//
        currentHost:'',//
        loading:false,
        currentApiUrl:'',
        isUseLocalData:undefined,
        isChangeApiCont:true
      }
    },
    beforeRouteEnter (to, from, next) {
      next(vm => {
        vm.currentApiUrl = vm.$store.state.activeApiUrl
        vm.onChangeApi(vm.currentApiUrl)
        vm.getApiByUrl(vm.currentApiUrl)
      })
    },
    created(){
      if(!this.userInfo.currentHost){
        this.$message.error('')
        return
      }
      this.initData()
    },
    mounted(){
      
    },
    computed: {
      ...mapState(['historyApiList','activeApiUrl','userInfo']),
    },
    methods: {
      ...mapActions(['setStore_historyApiList','setStore_activeApiUrl']),
      async clickUpdateApiList(){
        await this.getApiList()
        this.$message.success('')
        this.currentApiUrl = ''
        this.apiContStr = ''
      },
      async clickAllApiSetOrigin(isUseLocalData){
        await updateMany_apiDataByHost({isUseLocalData})
        if(isUseLocalData){
          setUseAllBackups()
          this.$message.success('-')
        }else{
          this.$message.success('')
        }
        this.currentApiUrl = ''
        this.apiContStr = ''
        await this.getApiList()
      },
      // 
      async clickAllSetBackups(){
        sendAddAllNewBackups()
        this.$message.success('')
      },
      async onBlurInputApiDesc ({_id,desc}){
        this.isEditingDesc = false
        await updateOne_apiData_byId(_id,{desc})
        this.$message.success('')
      },
      clickAddApi(){
        this.dialog.visible = true
        if(this.$refs.dialogForm){
          this.$refs.dialogForm.clearValidate()
        }
      },
      clickSubmitAddApi(){
        this.$refs.dialogForm.validate(valid=>{
          if (valid) {
            this.sendAddApi()
          } else {
            return false;
          }
        })
      },
      // 
      async sendAddApi(){
        let {path,apiCont,desc} = this.dialog.form||{};
        try{
          let apiJson = await format(apiCont)
          let jsonData = apiJson.replace(/[\n\s]/g,'')
          var newData = JSON.parse(jsonData)
          await addOne_api(path,newData,desc)
          this.apiList =  await getApiData({host:this.currentHost})
          this.dialog = {
            visible:false,
            form:{
              path:'',
              apiCont:''
            }
          }
        }catch(e){
          this.$message.error(e.message)
          return
        }
      },
      async initData(){
        this.getApiList();
      },
      // confirm promise
      _confirm(str){
        return new Promise((resolve,reject)=>{
          this.$confirm(str, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            resolve()
          }).catch(() => {
            reject()          
          });
        })
      },
      // 
      async saveEdit(){
        let jsonData = this.apiContStr.replace(/[\n\s]/g,'')
        try{
          var newData = JSON.parse(jsonData)
        }catch(e){
          this.$message.error('Json')
          return
        }
        let {_id} = this.apiInfo
        let resObj = await updateOne_apiData_byId(_id,{data:newData})
        this.$message.success('')
      },
      clickApiCont(){
        this.isChangeApiCont = true
      },
      // 
      onChangeApi(url){
        let obj = this.apiList.filter(v=>v.url==url)[0]
        if(!obj){
          return
        }
        this.setStore_historyApiList({url:obj.path,desc:obj.desc||''})
        // this.isChangeApiCont = false
        this.setStore_activeApiUrl(url)
        this.getApiByUrl(url)
      },
      // &
      clickEditApi(item){
        // this.historyDialog.visible = true
        let {path} = item
        this.$router.push({name:'Backups',query:{currentApiUrl:path}})
      },
      // path url
      clickChangeApi(item){
        this.dialogApi = {
          visible:true,
          form:{
            ...item,
            newPath:item.path
          }
        }
      },
      // api path 
      async submitApiChangeRes(){
        let {form} = this.dialogApi || {}
        if(form.newPath == form.path){
          this.$message.error('path')
          return
        }
        let resApiList = await getApiData({path:form.newPath})
        if(resApiList && resApiList.length){
          this.$message.error('')
          return
        }
        await updateOne_apiData_byId(form._id,{
          path:form.newPath,
          url:form.host + form.newPath
        })
        this.$message.success("")
        this.dialogApi.visible = false
        this.getApiList()

      },
      // 
      async updateApi(item){
        await this.getApiByUrl(item.url)
        this.$message.success('')
      },
      // 
      async clickRemoveApi(item){
        await this._confirm('')
        await removeOne_api(item.url)
        this.$message.success('')
        this.getApiList()
      },
      //   or 
      async onChange_isUseLocalData({_id,isUseLocalData}){
        await updateOne_apiData_byId(_id,{isUseLocalData})
        this.isUseLocalData = isUseLocalData
        this.$message.success('')
        this.getApiList()
      },
      
      // 
      async initCurrentHost(){
        let resObj = await getCurrentHost();
        this.currentHost = (resObj||{}).host
      },
      //  api 
      async getApiList(){
        this.apiList = await getApiData()
      },
      // 
      async getApiByUrl(url){
        let arr = await getApiData({url});
        let resObj = arr[0]||{};
        try{
          this.apiContStr = await format(JSON.stringify(resObj.data));
        }catch(e){
          console.error('apiContStr format Error',e)
        }
        this.apiInfo = resObj
        this.isUseLocalData = resObj.isUseLocalData
      },
    }
  }
</script>
<style rel="stylesheet/css" lang="css" scoped>
  .el-collapse-item__header{
    color:red !important;
  }
</style>


